html, body {
  height: 100%;
}

body, button {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 0;
}

ol, ul {
  padding-left: 30px;
}

.header {
  position: fixed;
  height: 40px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: cadetblue;
  color: white;
  font-weight: bold;
}

#root {
  height: calc(100% - 40px);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px 0 0;
}

.board-row {
  display: flex;
  justify-content: center;
  margin: -1px 0;
}

.status {
  margin-bottom: 16px;
  font-size: 2em;
  text-align: center;
}

.square {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30%;
  margin: -1px -1px 0 0;
  padding: 0;
  background: #fff;
  border: 1px solid #999;
  font-size: 4em;
  font-weight: bold;
}

.square.X {
  color: darkslategray;
}

.square.O {
  color: lightcoral;
}

.square::after {
  display: block;
  padding-bottom: 100%;
  content: "";
}

.square:focus {
  outline: none;
}

/* .kbd-navigation .square:focus {
  background: #ddd;
} */

.game {
  max-width: 400px;
  width: 100%;
}

.button {
  display: flex;
  justify-content: center;
  margin: 0 0 16px;
}

.button button {
  height: 32px;
  width: 80px;
  border-radius: 16px;
}